html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  height: 100%;
}
#login-con{
  min-height: 560px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fafafa;
}
#login {
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-box {
  position: relative;
  display: flex;
  height: 560px;
  background: #ffffff;
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.1);
  border-radius: 6px 8px 8px 6px;
}
.welcome-box {
  width: 500px;
  height: 560px;
  background-image: url(../../static/media/img_background@2x.2068124a.png);
  background-size: cover;
}
.welcome-text {
  margin: 67px 0 0 51px;
  font-size: 30px;
  font-weight: 500;
  color: #fff;
  line-height: 50px;
  text-align: left;
  white-space: pre-wrap;
}
.form {
  display: flex;
  flex-direction: column;
  margin: 50px 40px;
  padding: 64px 40px 0 40px;
  width: 340px;
  height: 460px;
  box-sizing: border-box;
}
.en-form {
  width: 375px;
}
.id,
.name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 16px;
}
.label {
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: #394256;
}
input {
  padding: 12px 16px;
  width: 260px;
  height: 44px;
  border-radius: 4px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  outline: none;
}
.en-input {
  width: 295px;
}
input::-webkit-input-placeholder {
  color: #b1b4bb;
}
input::-moz-placeholder {
  /* FF 19+ */
  color: #b1b4bb;
}
input:-moz-placeholder {
  /* FF 4-18 */
  color: #b1b4bb;
}
input:-ms-input-placeholder {
  /* IE 10+ */
  color: #b1b4bb;
}
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
#userName {
  height: 44px;
  font-size: 14px;
  font-weight: 400;
  font-family: PingFangSC-Regular, PingFang SC;
  color: #18191a;
  line-height: 44px;
}
.login-btn {
  align-self: center;
  margin-top: 84px;
  width: 190px;
  height: 48px;
  background-color: #3478fc;
  border-radius: 4px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  line-height: 48px;
  cursor: pointer;
}
button[class='login-btn']:disabled {
  opacity: 0.5;
}
.login-err-tip {
  display: none;
  margin-top: 8px;
  font-size: 12px;
  font-weight: 400;
  font-family: PingFangSC-Regular, PingFang SC;
  color: #f64326;
  line-height: 14px;
}

.btn {
  text-align: center;
  background-color: #3478fc;
  border-radius: 4px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
}
.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.err-tip {
  align-self: flex-start;
  margin-top: 8px;
  font-size: 12px;
  font-weight: 400;
  font-family: PingFangSC-Regular, PingFang SC;
  color: #f64326;
  line-height: 14px;
}

.err-icon {
  margin-right: 8px;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  width: 24px;
  height: 24px;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
*::-webkit-scrollbar-thumb {
  /*滚动条内部滑块*/
  border-radius: 3px;
  background-color: #bebebe;
  opacity: 0.6;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  cursor: pointer;
}

*::-webkit-scrollbar-thumb:hover {
  /*鼠标悬停滚动条内部滑块*/
  background: #bebebe;
}

*::-webkit-scrollbar-track {
  /*滚动条内部轨道*/
  background: transparent;
}
*::-webkit-scrollbar-corner {
  background: transparent;
}

.dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 360px;
  min-height: 214px;
  background-color: #fff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1),
    0px 12px 12px 0px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
}
.dialog .top {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 48px;
  padding: 0 42px;
  box-sizing: border-box;
}
.dialog .top .title {
  position: relative;
  padding: 0 18px;
  line-height: 48px;
  font-size: 14px;
  font-weight: 400;
  color: #000;
  height: 100%;
  cursor: pointer;
}
.dialog .top .title.actived {
  font-weight: 500;
}
.dialog .top .title.actived::after {
  content: '';
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  bottom: 0;
  width: 32px;
  height: 3px;
  background-color: #3478fc;
  border-radius: 4px;
}
.dialog .content {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 24px;
  width: 100%;
}
.dialog .content .peer,
.dialog .content .group,
.dialog .content .attend {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.dialog .content .peer .input-box,
.dialog .content .group .input-box,
.dialog .content .attend .input-box {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 34px;
}
.dialog .content .peer .btn-box,
.dialog .content .group .btn-box,
.dialog .content .attend .btn-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dialog .content .peer .btn-box .cancel-btn,
.dialog .content .group .btn-box .cancel-btn,
.dialog .content .attend .btn-box .cancel-btn,
.dialog .content .peer .btn-box .create-peer-btn,
.dialog .content .group .btn-box .create-peer-btn,
.dialog .content .attend .btn-box .create-peer-btn {
  width: 100px;
  height: 36px;
  line-height: 34px;
}
.dialog .content .peer .btn-box .cancel-btn,
.dialog .content .group .btn-box .cancel-btn,
.dialog .content .attend .btn-box .cancel-btn {
  margin-right: 20px;
  background-color: #fff;
  color: #3478fc;
  border: 1px solid #e8e8e8;
}
.dialog .content .peer .err-tip,
.dialog .content .group .err-tip,
.dialog .content .attend .err-tip {
  position: absolute;
  bottom: -14px;
}
.dialog .content input {
  margin-bottom: 8px;
  padding: 9px 0;
  width: 312px;
  height: 40px;
  border: none;
  border-bottom: 1px solid #ebecec;
}
.dialog .content .en-input {
  width: 410px;
}

