  .wrapper .dialog .close-icon::after {
    position: absolute;
    content: " ";
    height: 12px;
    width: 2px;
    background-color: #fff;
  }
  .wrapper .dialog .close-icon::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  .wrapper .dialog .close-icon::after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
